<template>
  <div>
    <el-row
      type="flex"
      class="row-bg"
      justify="space-around"
    >
      <el-col
        :span="13"
        class="borderDes"
      >
        <h4 style="margin-left: 20px;">直播通道列表</h4>
        <div style="margin-right: 20px; margin-left: 20px;">
          <el-form
            :inline="true"
            :model="form"
            :rules="relu"
            ref="formName"
          >
            <el-form-item
              label="通道名"
              prop="name"
            >
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item style="padding-left:20px">
              <el-button
                type="primary"
                icon="el-icon-plus"
                @click="live_add"
              >增加</el-button>
              <el-button
                type="primary"
                icon="el-icon-edit"
                :disabled="btnDisable.liveBtn.reName"
                @click="live_reName"
              >更名</el-button>

              <el-button
                type="danger"
                icon="el-icon-delete"
                :disabled="btnDisable.liveBtn.del"
                @click="live_del"
              >删除</el-button>
              <el-button
                type="warning"
                :disabled="btnDisable.liveBtn.init"
                @click="live_ini"
              >初始化</el-button>
              <el-button
                type="success"
                :disabled="btnDisable.liveBtn.save"
                @click="live_save"
              >保存</el-button>
            </el-form-item>

          </el-form>

          <el-row
            type="flex"
            class="row-bg"
            justify="space-around"
          >
            <el-col :span="5">

              <div
                class="treeDes"
                style="margin-right: 5px;"
              >
                <el-tree
                  default-expand-all
                  node-key="id"
                  :expand-on-click-node="false"
                  @node-click="clickTreeFunc"
                  :highlight-current="true"
                  ref="tree"
                  :data="treeData"
                ></el-tree>
              </div>
            </el-col>
            <el-col :span="19">
              <div>
                <edit-table
                  :table_ui_data="liveInfoUI"
                  :table_data="liveInfoTableData"
                  :table_disable_edit="false"
                  @dataChanged="liveTbChanged"
                  ref="liveTb"
                >
                </edit-table>
              </div>

            </el-col>
          </el-row>

        </div>

      </el-col>
      <el-col
        :span="10"
        class="borderDes"
      >
        <h4 style="margin-left: 20px;">直播服务器列表</h4>
        <div style="margin-right: 20px; margin-left: 20px;">
          <el-button
            type="warning"
            @click="server_ini"
          >初始化</el-button>
          <el-button
            type="primary"
            icon="el-icon-plus"
            @click="server_add"
            v-show="false"
          >增加</el-button>

          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="btnDisable.serverBtn.del"
            @click="server_del"
          >删除</el-button>

          <el-button
            type="success"
            :disabled="btnDisable.serverBtn.save"
            @click="server_save"
          >保存</el-button>

          <edit-table
            :table_ui_data="liveListUI"
            :table_data="liveListTableData"
            :table_disable_edit="false"
            @dataChanged="serverTbChaned"
            @rowClick="serverRowClick"
            
            ref="serverTb"
            style="margin-top: 20px; "
          >

          </edit-table>
        </div>
      </el-col>
    </el-row>
    <tip-dialog
      :dialog="dialog"
      @okClick="dialogOkClick"
    ></tip-dialog>
  </div>

</template>

<script>
import tipDialog from "../tipDialog.vue";
import { Message } from "element-ui";

let idNum = 1000;
export default {
  components: { tipDialog },

  data() {
    return {
      liveListUI: [
        {
          label: "线路",
          prop: "id",
          disabled: true,
        },
        { label: "服务器地址", prop: "address" },
        { label: "端口", prop: "port" },
        { label: "延迟(秒)", prop: "delay" },
      ],
      liveListTableData: [],
      liveInfoTableData: [],
      liveInfoUI: [
        {
          label: "线路",
          prop: "id",
          disabled: true,
        },
        { label: "组播地址", prop: "address" },
        { label: "端口", prop: "port",width:60 },
        { label: "车载组播地址", prop: "address_train" },
        { label: "车载端口", prop: "port_train",width:60 },
        { label: "源地址", prop: "src" },
        { label: "源端口", prop: "src_port",width:60 },
        { label: "包大小", prop: "package_size" ,width:60},
        { label: "是否缓冲", prop: "is_buffer",width:50 },
      ],
      treeData: [
        {
          id: 0,
          label: "视频直播通道",
          children: [],
        },
      ],
      form: {
        name: "",
      },
      relu: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
      },
      btnDisable: {
        liveBtn: {
          reName: true,
          del: true,
          init: true,
          save: true,
        },
        serverBtn: {
          del: true,
          save: true,
        },
      },
      treeCurrent: {},
      dialog: {
        visible: false,
        title: "提示",
        tipMsg: "",
        trsfParm: {},
      },
      lineList: [],
    };
  },

  methods: {
    dialogOkClick(param) {
      var that = this;
      if (param.num == 1) {
        let postDat = {
          name: this.treeCurrent.label,
        };
        this.$httpApi.live_del(postDat).then((res) => {
          if (res.data.code == "0") {
            const children = that.treeData[0].children;
            const index = children.findIndex(
              (d) => d.id === that.treeCurrent.id
            );
            children.splice(index, 1);

            that.liveInfoTableData = [];
            that.$refs.liveTb.initCurrentData(that.liveInfoTableData);
            if (children.length <= 0) {
              that.btnDisable.liveBtn.init =
                that.btnDisable.liveBtn.del =
                that.btnDisable.liveBtn.reName =
                  true;
            }
          } else {
            Message.error(res.data.message);
          }
        });
      } else if (param.num == 2) {
        let postDat1 = {
          old_name: this.treeCurrent.label,
          new_name: that.form.name,
        };
        this.$httpApi.live_rename(postDat1).then((res) => {
          if (res.data.code == "0") {
            if (that.treeCurrent && that.form.name.length > 0) {
              that.treeCurrent.label = that.form.name;
            }
          } else {
            Message.error(res.data.message);
          }
        });
      }
    },
    live_add() {
      var that = this;
      this.$refs.formName.validate((val) => {
        if (val) {
          const newChild = { id: idNum++, label: that.form.name, children: [] };

          that.treeData[0].children.push(newChild);
        }
      });
    },
    live_reName() {
      this.dialog.tipMsg =
        "是否更改 " + this.treeCurrent.label + " 为 " + this.form.name + "?";

      this.dialog.trsfParm.num = 2;
      this.dialog.visible = true;
    },
    live_del() {
      this.dialog.tipMsg = "是否删除通道 " + this.treeCurrent.label + "?";
      this.dialog.trsfParm.num = 1;
      this.dialog.visible = true;
    },
    live_ini() {
      this.liveInfoTableData = new Array();
      this.lineList.forEach((element) => {
        let obj = {
          id: element.id,
          address: "224.3.1.50",
          port: "7660",
          address_train: "224.3.1.50",
          port_train: "7661",
          src: "224.3.0.1",
          src_port: "1234",
          package_size: "1316",
          is_buffer: "0",
        };
        this.liveInfoTableData.push(obj);
      });
    },
    live_save() {
      console.log("列表执行");
      this.$refs.liveTb.confirmOKAction((obj, typeNum) => {
        var postData = JSON.parse(JSON.stringify(obj));
        postData.name = this.treeCurrent.label;
        if (typeNum == 1) {
          //增加
          console.log("add");
          this.$httpApi.live_add(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        } else if (typeNum == 2) {
          //变更
          console.log("upda");
          this.$httpApi.live_update(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        } else if (typeNum == 3) {
          //删除
          console.log("del");
          this.$httpApi.live_del(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        }
      });
    },
    server_add() {


      // this.$refs.serverTb.addAction({id:'',address:'',port:'',delay:'60'});
    },
    server_ini() {
      this.liveListTableData = new Array();
      let num = 10;
      this.lineList.forEach((element) => {
        let obj = {
          id: element.id,
          address: "10.0.50." + num++,
          port: "7670",
          delay: "60",
        };
        this.liveListTableData.push(obj);
      });
    },
    server_del() {
       this.$refs.serverTb.deleteAction();
    },
    server_save() {

        this.$refs.serverTb.confirmOKAction((obj, typeNum) => {
        var postData = JSON.parse(JSON.stringify(obj));
        
        if (typeNum == 1) {
          //增加
          console.log("add");
          this.$httpApi.live_server_add(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        } else if (typeNum == 2) {
          //变更
          console.log("upda");
          this.$httpApi.live_server_update(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        } else if (typeNum == 3) {
          //删除
          console.log("del");
          this.$httpApi.live_server_del(postData).then((resp) => {
            if (resp.data.code != "0") {
              Message.error(resp.data.message);
            }
          });
        }
      });
    },
    clickTreeFunc(trdata) {
      this.btnDisable.liveBtn.init =
        this.btnDisable.liveBtn.del =
        this.btnDisable.liveBtn.reName =
          trdata.id < 1000;

      this.treeCurrent = trdata;
      var that = this;
      that.liveInfoTableData = new Array();
      that.$refs.liveTb.initCurrentData(that.liveInfoTableData);
      this.$httpApi.live_list().then(function (response) {
        if (response.data.code == "0") {
          const listD = response.data.dataList;

          if (listD) {
            listD.forEach((element) => {
              if (element.name == trdata.label) {
                that.liveInfoTableData = element.dataList;

                that.$refs.liveTb.initCurrentData(that.liveInfoTableData);
              }
            });
          }
        }
      });
    },
    liveTbChanged(val) {
      this.btnDisable.liveBtn.save = val == 0;
    },
    serverTbChaned(val) {
      this.btnDisable.serverBtn.save = val == 0;
    },
    serverRowClick(obj) {
      this.btnDisable.serverBtn.del = obj == undefined;
    },
  },
  mounted() {
    const that = this;

    this.$httpApi.lineInfo("0").then(function (response) {
      if (response.data.code == "0") {
        that.lineList = response.data.dataList;
      }
    });

    this.$httpApi.live_list().then(function (response) {
      if (response.data.code == "0") {
        let idNu = 3000;
        const listD = response.data.dataList;

        if (listD) {
          listD.forEach((element) => {
            that.treeData[0].children.push({
              label: element.name,
              id: idNu++,
              tableData: element.dataList,
            });
            if (idNu == 3001) {
              console.log(that.treeData[0].children[0].id);
              that.$refs.tree.setCurrentKey(that.treeData[0].children[0].id);
            }
          });
        }
      }
    });

    this.$httpApi.live_server_list().then((res) => {
      if (res.data.code == "0") {
        that.liveListTableData = res.data.dataList;
        that.$refs.serverTb.initCurrentData(that.liveListTableData);
      } else {
        Message.error(res.data.message);
      }
    });
  },
};
</script>

<style scoped>
.borderDes {
  border: 1px solid lightgray;
  height: 90vh;
  margin-bottom: 20px;
}

.treeDes {
  border: 1px solid lightgray;
  max-height: 500px;
  min-height: 300px;
}
</style>